const React = require('react')
import { Link } from 'react-router'

export default class NotFound extends React.Component {
    render() {
        return (
            <div>
                <h1>404</h1>
                <h2>Page not found!</h2>
                <p>
                    <Link to="/">Go back to the main page</Link>
                </p>
            </div>
        )
    }
}


